import React from 'react';
import Radium from 'radium';

const generate_texts = (props) =>{
  const change_button_style = {
    backgroundColor: "yellow",
    color: "black",
    ":hover": {
      textDecoration: "underline"
    },
  }

  const clear_button_style = {
    backgroundColor: "lightblue",
    color: "black",
    ":hover": {
      textDecoration: "underline"
    },
  }

    return (
          <li id = "first_quest">
            {props.quest} &nbsp;&nbsp;
            <button style = {change_button_style} onClick = {props.change_quest} key = "change_button">change</button>
            <button style = {clear_button_style} onClick = {props.clear_quest} key = "clear_button">clear</button>
          </li>
    )
}

export default Radium(generate_texts);



