import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Radium from "radium"
import GenerateTexts from './quest_texts/quest_texts'


class App extends Component {
  
  state = {
    button_enable: true,
    quests_array: [
      {id: "asdf0", text: "Jog for 10 Minutes"},
      {id: "asdf1", text: "Read A Chapter of A Book"},
      {id: "asdf2", text: "Clean your room"},
      {id: "asdf3", text: "Do some stretches from the following URL: https://ehs.ucsc.edu/programs/ergo/stretch.html"},
      {id: "asdf4", text: "get breakfast"},
      {id: "asdf5", text: "No youtube before sleeping or getting up"},
    ],
    quests_on_screen: [],
    quest_on_adding: "",

  }

  get_single_random_quest = () => {
    function getRndInteger(min, max) {
      return Math.floor(Math.random() * (max - min) ) + min;
    }
    let random_quest;
    while (true){
      random_quest = this.state.quests_array[getRndInteger(0, this.state.quests_array.length)].text;
      if (!this.state.quests_on_screen.map(x => x.text).includes(random_quest)){
        break;
      }
    }
    return random_quest;
  }

  set_quests_on_screen = () =>{
    const generate_random_quest_lists = (arr, n) => {
      function getRndInteger(min, max) {
          return Math.floor(Math.random() * (max - min) ) + min;
      }
      if (n >= arr.length){
        return arr;
      }
      let new_arr = [];
      for (let i = 0; i < n; i++){
          let index = getRndInteger(0, arr.length);
          if (new_arr.includes(arr[index])){
              i--;
          } else {
              new_arr.push(arr[index])
          }
      }
      return new_arr;
    }

    this.state.quests_on_screen = generate_random_quest_lists(this.state.quests_array, 3);
  }

  add_slot_change_handler = (event) => {
    const quest_on_add_slot = event.target.value;
    this.state.quest_on_adding = quest_on_add_slot;
  }

  add_quest = () => {
    console.log(this.state.quest_on_adding);
  }


  render() {
    const confirm_button_style = {
      backgroundColor: "green",
      color: "white",
      fontWeight: "bold",
      fontSize: "15px",
      padding: "5px",
      ':hover': {
        backgroundColor: 'lightgreen',
        color: "black",
      }
    }

    
    let contents;
    if (this.state.button_enable){
      this.set_quests_on_screen();
      contents = <button 
        style = {confirm_button_style}
        onClick = {() => this.setState({button_enable: false})}>Confirm</button>
    } else {
      contents = (
        <ul className = "quest_lists">
          {this.state.quests_on_screen.map((quest, index) => {
            let change_quest = () => {
              let new_quest = this.get_single_random_quest();
              let quests_on_screen = this.state.quests_on_screen.slice();
              quests_on_screen[index] = {id:"adfae24", text: new_quest};
              this.setState({quests_on_screen: quests_on_screen});
            }

            let clear_quest = () => {
              const quests_on_screen = [...this.state.quests_on_screen];
              quests_on_screen.splice(index,1);
              this.setState({quests_on_screen: quests_on_screen});
            }

            return <GenerateTexts
                quest = {quest.text}
                change_quest = {change_quest}
                clear_quest = {clear_quest}
                key = {quest.id}/>

          })}
        </ul>
      )
    }

    return (
      <div className="App">
          <h1>Daily Quest</h1>
          <p>Ready to get your daily quests</p>
          {contents}
          <div id = "add_view_remove">
            <input type = "text" onChange = {(event) => this.add_slot_change_handler(event)}></input><button onClick = {this.add_quest}>Add</button>
          </div>
          

          {/* <button id = "create_button">Create</button>
          <button id = "view_button">View</button>
          <button id = "remove_button">Remove</button> */}

        </div>
    )
  }
}

export default Radium(App);



